import pic_5 from "../../image/pic_5.jpg";
import pic_6 from "../../image/pic_6.jpeg";
import pic_7 from "../../image/pic_5.jpg";
import pic_8 from "../../image/pic_5.jpg";
import pic_9 from "../../image/pic_5.jpg";
import pic_10 from "../../image/pic_10.jpg";
import { changableText } from "./Text";

const services = [
  { image: pic_5, description: "description1" },
  { image: pic_5, description: "description2" },
  { image: pic_7, description: "description3" },
  { image: pic_8, description: "description4" },
  { image: pic_9, description: "description5" },
  { image: pic_10, description: "description6" },
];

export function ServicesComponent({ selectedLanguage }) {
  return (
    <div className="services">
      {services.map((s, i) => (
        <div key={i} className="img-wrapper">
          <img src={s.image} />
          <div className="desc">{s.description}</div>
        </div>
      ))}
    </div>
  );
}
